import React, { Component } from "react";
import PropTypes from "prop-types";
import { Collapsible, SvgIcons } from "@dnnsoftware/dnn-react-common";
import resx from "../../resources";
import "./capacitySettings.less";

class CapacityRow extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false
        };
    }

    componentDidUpdate(prevProps) {
        if (this.props.openId !== prevProps.openId) {
            this.setState({
                isOpen: this.props.openId === this.props.id
            });
        }
    }

    toggle() {
        const { onEdit } = this.props;
        if (!this.state.isOpen) {
            onEdit();
        }
    }

    onDelete(event) {
        event.stopPropagation();
        if (this.props.onDelete) {
            this.props.onDelete();
        }
    }

    render() {
        const { capacity } = this.props;
        const statusClass = capacity.IsEnabled ? "status-enabled" : "status-disabled";
        const statusText = capacity.IsEnabled ? resx.get("Enabled") : resx.get("Disabled");

        return (
            <div className="capacity-row">
                <div className="capacity-row-header" onClick={this.toggle.bind(this)}>
                    <div className="capacity-info">
                        <span className="capacity-name">{capacity.CapacityDisplayName}</span>
                        <span className="capacity-id">({capacity.CapacityName})</span>
                        <span className={`capacity-status ${statusClass}`}>{statusText}</span>
                    </div>
                    <div className="capacity-actions">
                        <button
                            className="delete-button"
                            onClick={this.onDelete.bind(this)}
                            title={resx.get("Delete")}>
                            <SvgIcons.TrashIcon />
                        </button>
                    </div>
                </div>
                <Collapsible
                    isOpened={this.state.isOpen}
                    className="capacity-row-details">
                    <div className="capacity-details">
                        <div className="detail-row">
                            <label>{resx.get("Description")}:</label>
                            <span>{capacity.Description || resx.get("NoDescription")}</span>
                        </div>
                        <div className="detail-row">
                            <label>{resx.get("AzureManagementCapacityName")}:</label>
                            <span>{capacity.AzureManagementCapacityName}</span>
                        </div>
                        <div className="detail-row">
                            <label>{resx.get("AzureManagementResourceGroup")}:</label>
                            <span>{capacity.AzureManagementResourceGroup}</span>
                        </div>
                        <div className="detail-row">
                            <label>{resx.get("AzureManagementSubscriptionId")}:</label>
                            <span>{capacity.AzureManagementSubscriptionId}</span>
                        </div>
                        <div className="detail-row">
                            <label>{resx.get("AzureManagementPollingInterval")}:</label>
                            <span>{capacity.AzureManagementPollingInterval} {resx.get("Minutes")}</span>
                        </div>
                    </div>
                </Collapsible>
            </div>
        );
    }
}

CapacityRow.propTypes = {
    capacity: PropTypes.object.isRequired,
    id: PropTypes.number.isRequired,
    openId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    onEdit: PropTypes.func.isRequired,
    onDelete: PropTypes.func.isRequired,
    Collapse: PropTypes.func.isRequired
};

export default CapacityRow;
